.details-container{
    display: flex;
    justify-content: space-between;
    padding-left:120px;
    padding-right:120px;
}
.name-details{
    width: 486px;
    margin-top:54px;
}
.role{
    font-weight: 700;
font-size: 20px;
line-height: 27px;
text-transform: uppercase;
color: #FDC435;
}
.full-name{
    font-weight: 700;
font-size: 64px;
line-height: 120%;
color: #25282B;
font-family: 'Playfair Display';
}
.details{
    font-weight: 400;
font-size: 24px;
line-height: 150%;
margin-top:32px;

color: #828282;
}
.buttons{
    display: flex;
    margin-top:32px;
}
.project-button{
    display: flex;
flex-direction: row;
align-items: flex-start;
padding: 8px 24px;
margin-right:12px;

height: 43px;
font-weight: 500;
font-size: 18px;
line-height: 150%;


color: #25282B;
border:0px;
background: #FDC435;
border-radius: 8px;
}
.linkedin-button{
    display: flex;
flex-direction: row;
align-items: flex-start;
padding: 8px 24px;

height: 43px;
font-weight: 500;
font-size: 18px;
line-height: 150%;

color: #25282B;

border: 2px solid #25282B;
border-radius: 8px;
}